@import url(https://fonts.googleapis.com/css2?family=Noto+Sans+JP:wght@400;700&display=swap);
* {
  box-sizing: border-box;
}

html {
  font-family: "Noto Sans JP", sans-serif;
}

body {
  margin: 0;
  background-color: #3f3f3f;
}

.chart {
  padding: 1rem;
  border-radius: 12px;
  background-color: #4b4b4b;
  text-align: center;
  display: flex;
  justify-content: space-around;
  height: 10rem;
}

.chart-bar {
  height: 100%;
  display: flex;
  flex-direction: column;
  align-items: center;
}
.chart-bar__inner {
  height: 100%;
  width: 100%;
  border: 1px solid white;
  border-radius: 12px;
  background-color: #d0240e;
  overflow: hidden;
  display: flex;
  flex-direction: column;
  justify-content: flex-end;
}
.chart-bar__fill {
  background-color: #2a2a2a;
  width: 100%;
  transition: all 0.3s ease-out;
}

.chart-bar__label {
  font-weight: bold;
  font-size: 0.5rem;
  text-align: center;
  color: white;
}

.expense-date {
  display: flex;
  flex-direction: column;
  width: 5.5rem;
  height: 5.5rem;
  border: 1px solid #ececec;
  background-color: #2a2a2a;
  color: white;
  border-radius: 12px;
  align-items: center;
  justify-content: center;
}

.expense-date__month {
  font-size: 0.75rem;
  font-weight: bold;
}

.expense-date__year {
  font-size: 0.75rem;
}

.expense-date__day {
  font-size: 1.5rem;
  font-weight: bold;
}

.card {
  border-radius: 12px;
  box-shadow: 0 1px 8px rgba(0, 0, 0, 0.25);
}

.expense-item {
  display: flex;
  justify-content: space-between;
  align-items: center;
  padding: 0.5rem;
  margin: 1rem 0;
  background-color: #4b4b4b;
}

.expense-item__description {
  display: flex;
  flex-direction: column;
  grid-gap: 1rem;
  gap: 1rem;
  align-items: flex-end;
  flex-flow: column-reverse;
  justify-content: flex-start;
  flex: 1 1;
}

.expense-item h2 {
  color: #3a3a3a;
  font-size: 1rem;
  flex: 1 1;
  margin: 0 1rem;
  color: white;
}

.expense-item__price {
  font-size: 1rem;
  font-weight: bold;
  color: white;
  background-color: rgba(255, 0, 0, 0.753);
  border: 1px solid white;
  padding: 0.5rem;
  border-radius: 12px;
  transition: background-color 0.3s ease-in;
}
.expense-item__price:hover {
  background-color: white;
  color: rgba(255, 0, 0, 0.753);
  border-color: rgba(255, 0, 0, 0.753);
}

@media (min-width: 580px) {
  .expense-item__description {
    flex-direction: row;
    align-items: center;
    justify-content: flex-start;
    flex: 1 1;
  }

  .expense-item__description h2 {
    font-size: 1.25rem;
  }

  .expense-item__price {
    font-size: 1.25rem;
    padding: 0.5rem 1.5rem;
  }
}

.react_btn {
  padding: 12px 15px;
  outline: 0;
  background-color: #ffffff;
  color: #2a2a2a;
  transition: all 0.5s ease;
  cursor: pointer;
  font-weight: 600;
  border: #2a2a2a solid 2px;
  border-radius: 8px;
  font-size: 16px;
  margin: 0 4px;
  display: flex;
  align-items: center;
  justify-content: center;
}

.react_btn:hover {
  background-color: #2a2a2a;
  color: white;
}

.success {
  background-color: #085a0f !important;
  color: #ffffff !important;
}

.expenses {
  padding: 1rem;
  background-color: rgb(31, 31, 31);
  margin: 2rem auto;
  width: 50rem;
  max-width: 95%;
}

.message {
  text-align: center;
  color: white;
  font-weight: 500;
  font-size: 22px;
}

.expenses-filter {
  color: white;
  padding: 0 1rem;
}

.expenses-filter__control {
  display: flex;
  width: 100%;
  align-items: center;
  justify-content: space-between;
  margin: 1rem 0;
}

.expenses-filter label {
  font-weight: bold;
  margin-bottom: 0.5rem;
}

.expenses-filter select {
  font: inherit;
  padding: 0.5rem 3rem;
  font-weight: bold;
  border-radius: 6px;
  border: 0;
  background-color: rgba(255, 0, 0, 0.753);
  color: white;
}
.expenses-filter select:focus {
  outline: 0;
}

/* Hello World */

.new_expense_controls {
  display: flex;
  flex-flow: row wrap;
  align-items: center;
  justify-content: space-between;
}

.new_expense_control {
  width: 50%;
  padding: 5px 15px;
}
.new_expense_control label {
  font-size: 20px;
  font-weight: 600;
  font-family: Arial, Helvetica, sans-serif;
  letter-spacing: 3px;
}

.new_expense_control input {
  outline: 0;
  border: none;
  width: 100%;
  font-size: 18px;
  margin: 5px 0;
  border-radius: 5px;
  padding: 10px 5px;
  background-color: #2a2a2a;
  color: #ffff;
  font-weight: 600;
}
.new_expense_action {
  width: 50%;
  width: 50%;
  display: flex;
  align-items: center;
  margin: 4% 0 0;
  padding: 0 2%;
}

@media screen and (max-width: 576px) {
  .new_expense_controls {
    flex-flow: column wrap;
  }
  .new_expense_control,
  .new_expense_action {
    width: 100%;
  }

  .new_expense_control input {
    width: 100%;
  }
}

.new-expense {
  padding: 1rem;
  width: 50rem;
  max-width: 95%;
  margin: 1rem auto;
  background-color: rgba(255, 0, 0, 0.753);
  padding: 1rem;
  display: flex;
  align-items: center;
  justify-content: center;
}

